@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.container.home {
  background: url(/tours/static/media/concert2.ad74a91c.jpg) no-repeat center center fixed;
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
}

.container.about {
  background: url(/tours/static/media/concert3.06ec7e90.jpg) no-repeat center center fixed;
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
}

.container.tickets {
  background: url(/tours/static/media/concert5.cfcc19b0.jpg) no-repeat center center fixed;
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  background-size: cover;
}

.container.loader {
  background-color: hsla(120, 100%, 75%, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1%;
  padding-top: 250px;
  min-width: 90%;
  min-height: 90%;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.middle {
  font-family: "Neucha", cursive;
}

.home-content {
  margin: 2rem;
  padding: 4rem;
}

:root {
  --mainDark: #343a40;
  --mainGrey: rgba(109, 109, 109);
  --mainLightGrey: rgba(172, 172, 172);
  --mainWhite: #fff;
  --mainBlue: #007bff;
}

.svg_icons {
  transform: scale(1.8);
}

.tables {
  font-family: "Shadows Into Light", cursive;
  font-size: 50px;
}

.navbar-style {
  font-family: "Orbitron", sans-serif;
}

